import React, { Component } from 'react'
import './styles.css'
import { Redirect} from 'react-router-dom'
import {authAxios} from '../App'
import { localhost } from "../Config"

export class Landing extends Component {
    state = {
        user: '',
        name: '',
        email: '',
        address: '',
        old_password: '',
        new_password: '',
        show1: false,
        show2: false,
        show3: false,
        show4: false,
    }
    componentDidMount(){
        this.handleFetchUser()
    }
    handleFetchUser = () => {
    authAxios.get(`${localhost}/api/getuser/${localStorage.getItem('id')}`)
    .then(res => {
        console.log(res.data)
        this.setState({user: res.data})
    })
    .catch(err => {
        console.log(err)
    })
    }
     handleChange(evt) {
        this.setState({ [evt.target.name]: evt.target.value });
      }
    handleSubmit  = (event) =>{
        event.preventDefault()
        const userData = { first_name: this.state.name };
        authAxios.put(`${localhost}/api/user-name-update/${localStorage.getItem('id')}`,userData)
        .then(res=> {
            this.setState({name: ''})
            this.setState({ show1: false})
            this.handleFetchUser()
        })
        .catch(err => {
            console.log(err)
        })
    } 
    handleSubmit2  = (event) =>{
        event.preventDefault()
        const userData = { email: this.state.email };
        authAxios.put(`${localhost}/api/user-email-update/${localStorage.getItem('id')}`,userData)
        .then(res=> {
            this.setState({email: ''})
            this.setState({ show2: false})
            this.handleFetchUser()
        })
        .catch(err => {
            console.log(err)
        })
    }
    handleSubmit3  = (event) =>{
        event.preventDefault()
        const userData = { address: this.state.address };
        authAxios.put(`${localhost}/api/user-update/${localStorage.getItem('id')}`,userData)
        .then(res=> {
            this.setState({address: ''})
            this.setState({ show3: false})
            this.handleFetchUser()
        })
        .catch(err => {
            console.log(err)
        })
    }
    handleSubmit4  = (event) =>{
        event.preventDefault()
        const userData = { old_password: this.state.old_password, new_password: this.state.new_password};
        authAxios.put(`${localhost}/api/change-password/`,userData)
        .then(res=> {
            this.setState({old_password: '', new_password: ''})
            this.setState({ show4: false})
            this.handleFetchUser()
        })
        .catch(err => {
            console.log(err)
        })
    }
    handleLogout = () => {
    localStorage.removeItem('access')
    localStorage.removeItem('id')
    window.location.reload()
    }
    handleDelete = () => {
        authAxios.delete(`${localhost}/api/user-delete/${localStorage.getItem('id')}`)
        .then(res => {
            localStorage.removeItem('access')
            localStorage.removeItem('id')
            window.location.reload()
        })
        .catch(err => {
            console.log(err)
        })
    }
    render() {
    if (localStorage.getItem('access') === null) {
        return <Redirect to='/login' />
        }
    return (
        <div>
            <div className='landing-main'>
                <h1>Profile Details</h1>
            <div className='landing-content'>
            <h2>Name:</h2><h2> {this.state.user.first_name}&nbsp;&nbsp;<button onClick={() => this.setState({show1: true})}>edit</button></h2>
            </div>
            {this.state.show1 && (
                <form onSubmit={this.handleSubmit}>
                <label>
                Name
                <input
                    type="text"
                    name='name'
                    value={this.state.name}
                    onChange={this.handleChange.bind(this)}
                />
                </label>
                <button type='submit'>Submit</button> 
            </form>
            )}
            <div className='landing-content'>
            <h2>Email:</h2><h2>{this.state.user.email}&nbsp;&nbsp;<button onClick={() => this.setState({show2: true})}>edit</button></h2>
            </div>
            {this.state.show2 && (
                <form onSubmit={this.handleSubmit2}>
                <label>
                Email
                <input
                    type="email"
                    name='email'
                    value={this.state.email}
                    onChange={this.handleChange.bind(this)}
                />
                </label>
                <button type='submit'>Submit</button> 
            </form>
            )}
            <div className='landing-content'>
            <h2>Address:</h2><h2> {this.state.user.address}&nbsp;&nbsp;<button onClick={() => this.setState({show3: true})}>edit</button></h2>
            </div>
            {this.state.show3 && (
                <form onSubmit={this.handleSubmit3}>
                <label>
                Address
                <input
                    type="text"
                    name='address'
                    value={this.state.address}
                    onChange={this.handleChange.bind(this)}
                />
                </label>
                <button type='submit'>Submit</button> 
            </form>
            )}
            <div className='landing-content'>
                <button onClick={() => this.setState({show4: true})}>Reset Password</button>
            </div>
            {this.state.show4 && (
                <form onSubmit={this.handleSubmit4}>
                <label>
                Old password
                <input
                    type="password"
                    name='old_password'
                    value={this.state.old_password}
                    onChange={this.handleChange.bind(this)}
                />
                </label>
                <label>
                New password
                <input
                    type="password"
                    name='new_password'
                    value={this.state.new_password}
                    onChange={this.handleChange.bind(this)}
                />
                </label>
                <button type='submit'>Submit</button> 
            </form>
            )}
            <div className='landing-content'>
            <button onClick={this.handleDelete}>Delete Account</button>
            <button onClick={this.handleLogout}>logout</button>
            </div>
            </div>
        </div>
    )
    }
}

export default Landing
