import React,{useState} from 'react'
import './styles.css'
import axios from 'axios'
import { localhost } from "../Config"
import { Link,Redirect } from 'react-router-dom'
import { Formik, Form } from 'formik';
import { TextField } from './TextField';
import * as Yup from 'yup'

function Register(props) {
    const [accountCreated, setAccountCreated] = useState(false);
    const validate = Yup.object({
        name: Yup.string()
        .required('Email is required'),
        email: Yup.string()
        .email('Email is invalid')
        .required('Email is required'),
        password: Yup.string()
        .min(6, 'Password must be at least 6 charaters')
        .required('Password is required'),
        re_password: Yup.string()
        .oneOf([Yup.ref('password'), null], 'Password must match')
        .required('Confirm password is required'),
        address: Yup.string()
        .required('Email is required'),
    })

    const onFinish = (values) => {
      console.log(values.address)
    const userData = { email: values.email,first_name: values.name, password: values.password, address: values.address};
    axios.post(`${localhost}/api/register/`, userData)
    .then(res => {
        console.log(res.data)
    })
    .catch(err => {
        console.log(err)
    })
    setAccountCreated(true);
    }
    if (localStorage.getItem('access') !== null) {
        return <Redirect to='/' />
    }
    if (accountCreated) {
        return <Redirect to='/login' />
    }
    return (
        <div className='login-container'>
        <Formik
           initialValues={{
             name:'',
             email: '',
             password: '',
             re_password: '',
             address: '',
             }}
           validationSchema={validate}
           onSubmit={onFinish}
         >
           {formik => (
             <div>
               <Form className="sign-in-form">
               <h2>Sign up</h2>
                 <TextField label='Name' name='name' type='name' />
                 <TextField label="Email" name="email" type="email" />
                 <TextField label="Password" name="password" type="password" />
                 <TextField label="Confirm Password" name="re_password" type="password" />
                 <TextField label='Address' name='address' type='address' />
                 <button className="btn btn-dark mt-3" type="submit">Register</button>
                 <Link to='/login'>Login</Link>
               </Form>
             </div>
           )}
         </Formik>
       </div>
    )
}

export default Register
